.post {
    width: 100%;
    border-radius: 10px;
    box-shadow: 0 0 16px -8px rgba(0, 0, 0, 0.68);
    margin: 30px 0;
  }
  .postWrapper {
    padding: 10px;
  }
  .postTop {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .postTopLeft {
    display: flex;
    align-items: center;
  }
  .postImg {
    width: 32px;
    height: 32px;
    border-radius: 50%;
    object-fit: cover;
  }
  .postName {
    font-size: 15px;
    font-weight: 500;
    margin: 0 10px;
  }
  .postDate {
    font-size: 12px;
  }
  .postCenter {
    margin: 20px 0;
  }
  .postCenterImg {
    width: 100%;
    margin-top: 20px;
    max-height: 500px;
    object-fit: contain;
  }
  .postBottom {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .postBottomleft {
    display: flex;
    align-items: center;
  }
  .likeIcon {
    width: 24px;
    height: 24px;
    margin-right: 5px;
    cursor: pointer;
  }
  .postLikeCoutner {
    font-size: 15px;
  }
  .postComment {
    cursor: pointer;
    border-bottom: 1px dashed grey;
    font-size: 15px;
  }