.rightbar {
    flex: 3.5;
  }
  .rightbarWrapper {
    padding: 20px 20px 0 0;
  }
  .rightbarContainer {
    display: flex;
    align-items: center;
  }
  .bithdayImg {
    width: 40px;
    height: 40px;
    margin-right: 10px;
  }
  .birthdayText {
    font-weight: 300;
    font-size: 15px;
  }
  .rightbarAd {
    width: 100%;
    border-radius: 10px;
    margin: 30px 0;
  }
  .rightBarTitle {
    margin: 20px 0;
  }
  .friendlist {
    padding: 0;
    margin: 0;
    list-style: none;
  }
  .rightbarFriend {
    display: flex;
    margin-bottom: 20px;
  }
  .rightbarImgContainer {
    margin-right: 10px;
    position: relative;
  }
  .profileimg {
    width: 40px;
    height: 40px;
    object-fit: cover;
    border-radius: 50%;
  }
  .rightbarOnline {
    width: 12px;
    height: 12px;
    border-radius: 50%;
    background-color: limegreen;
    position: absolute;
    top: -2px;
    right: 0;
    border: 2px solid white;
  }
  .rightbarUsername {
    font-weight: 500;
    margin: 10px 0;
  }
  
  /*------------------*/
  .rightbarTitle {
    font-size: 18px;
    font-weight: 500;
    margin-bottom: 10px;
  }
  .rightbarInfo {
    margin-bottom: 30px;
  }
  .rightbarInfoItem {
    margin-bottom: 10px;
  }
  .rightbarInfoKey {
    font-weight: 500;
    margin-left: 15px;
    color: #555;
  }
  .rightbarFollName {
    font-weight: 300;
  }
  .rightbarFollowings {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
  .rightbarFollowing {
    display: flex;
    flex-direction: column;
    margin-bottom: 20px;
  }
  .rightbarFollImg {
    width: 100px;
    height: 100px;
    object-fit: cover;
    border-radius: 5px;
  }